.subscribe {
  & p {
    margin: 0;
  }

  &__message {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    height: 50px;
    margin-top: 30px;
    padding: 0 10px;

    font-size: 14px;
    font-weight: 700;
    color: #ffffff;
    line-height: 18px;

    &--error {
      background-color: #ac1313;
    }

    &--success {
      background-color: #42c022;
    }

    &--hidden {
      display: none;
    }
  }
}

.subscribe__fields-wrapper {
  display: flex;
  justify-content: space-between;
  height: 40px;
  padding-top: 30px;

  & button {
    min-width: 110px;
    height: 100%;
    font-family: "Info", "Arial", sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;

    background-color: #ff5f0e;
    border: none;

    cursor: pointer;

    &:hover {
      background-color: #42c022;
      // background-color: #20ca25;
      transition: all 400ms ease;
    }
  }

  & input {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin-right: 10px;
    padding: 8px 12px 8px 40px;

    font-size: 14px;

    background-image: url("../img/icon-mail.png");
    background-position: 9px 57%;
    background-size: 25px;
    background-repeat: no-repeat;

    border: 1px solid #707070;
    border-radius: 0;
  }
}
